import React from 'react';

const Tomato = (props) => {
  return (
    <div className="box">
      <div className="tomato">
      <div className="highlight"></div>
      <div className="stem"></div>
      <div className="triangle"></div>
      </div>
    </div>
    )
}

export default Tomato


